import store from "@/store";
// axios 不能获取文本流 只能再封装一个 fatch请求
const baseURL = "https://api.openai.com";
const headers = {
  // eslint-disable-next-line
  "Authorization": "Bearer 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",
  "Content-Type": "application/json",
};
// 请求拦截器
function interceptRequest(url, options) {
  return Promise.resolve(options);
}

// 响应拦截器
function interceptResponse(response) {
  // console.log("Intercepting response:", response);
  return response;
}

// 发送请求
function sendRequest(url, options) {
  return interceptRequest(url, options) // 拦截请求
    .then((newOptions) => {
      return fetch(url, newOptions); // 发送请求
    })
    .then((response) => {
      if (response.ok) {
        return interceptResponse(response); // 拦截响应
      } else {
        throw new Error(
          "Failed to fetch data. Status code: " + response.status
        );
      }
    })
    .then((data) => {
      return data;
    })
    .catch((error) => {
      console.error(error);
    });
}
export default (url, method, submitData) => {
  // 判断是否为空
  if (!store.state.stopTextStream) {
    let controller = new AbortController();
    store.commit("stopTextStream", controller); // 存在vuex 里边  方便使用
  }
  const signal = store.state.stopTextStream.signal;
  return sendRequest(`${baseURL}${url}`, {
    method: method,
    headers: headers,
    body: JSON.stringify(submitData),
    responseType: "stream", // 这个也必须设置
    signal, // 停止文本流
  });
};
