    <div class="chatSearch">
      <!--根据语音获取文本组件  并通过语法糖获取文本
      遮罩层的状态传递给 获取文本组件用来 在获取完毕时 解放切换语音还是键盘输入
      -->
      <GetRecording
        :RecordingStatus="RecordingStatus"
        @RecordingText="RecordingText"
        @switchVoice="switchVoice"
        :MaskLayerShow="MaskLayerShow"
      ></GetRecording>
      <el-card shadow="hover">
        <!--加载图片-->
        <img :src="imgLoad" alt="" class="imgLoad" v-if="MaskLayerShow" />
        <!--判断是否是按下了回车-->
        <Input
          ref="inputInstance"
          v-model.trim="chatInput"
          @on-keydown="
            (e) => {
              // 判断是否是按下了回车
              if (e.keyCode === 13) {
                enterEvent();
              }
            }
          "
          enter-button
          @on-focus="scrollToBottom()"
          :border="false"
          placeholder="说点什么..."
        >
          <template #append>
            <Icon type="ios-paper-plane-outline" @click="enterEvent()" />
          </template>
        </Input>
        <!--语音遮罩层-->
        <PhoneticStyle
          @update:RecordingStatus="RecordingStatus = $event"
          v-if="WhetherToDisplayOrNot"
        ></PhoneticStyle>
        <!--遮罩层-->
        <div class="MaskLayer" v-if="MaskLayerShow">
          <span @click="StopGenerate">停止生成</span>
        </div>
      </el-card>
    </div>
