<script>
import { reactive, ref, onMounted, watch } from "vue";
import { useStore } from "vuex";
// 请求
import { chatGPTList } from "@/api/fetchChat";
import imgLoad from "@/assets/images/加载中.gif";
import ImPicture from "@/assets/images/Im.png";
// 组件
import Head from "@/views/chatGPT/components/Head";
import { ElMessage } from "element-plus";
// 右边的进度条
import rightFunction from "@/views/chatGPT/components/rightFunction";
// 获取语音组件
import GetRecording from "@/views/chatGPT/components/GetRecording";
// 语音遮罩层
import PhoneticStyle from "@/views/chatGPT/components/PhoneticStyle";
export default {
  name: "chatHome",
  components: { Head, rightFunction, GetRecording, PhoneticStyle },
  setup() {
    const chatList = reactive(
      JSON.parse(localStorage.getItem("chat"))
        ? JSON.parse(localStorage.getItem("chat"))
        : []
    ); // 定义发送数据
    const store = useStore(); // vuex实例
    let chatInput = ref(""); // 定义数据框内容
    let MaskLayerShow = ref(false); // 遮罩层是否显示
    let inputInstance = ref(null); // input ref实例
    let WhetherToDisplayOrNot = ref(null); // 是否显示语音遮罩层
    // 从子组件传递过来的 按下状态传递给他的兄弟组件
    let RecordingStatus = ref(null);
    // 输入框回车事件
    const enterEvent = () => {
      store.commit("stopTextStream", null); // 清空停止文本流 不然就不会发起请求了
      if (chatInput.value === "") return (chatInput.value = ""); // 如果输入框为空
      inputInstance.value.blur(); // 失去焦点
      MaskLayerShow.value = true; // 打开遮罩层
      // 输入框内容发送到后台
      chatList.push({
        role: "user",
        content: chatInput.value,
      });
      localStorage.setItem("chat", JSON.stringify(chatList));
      chatInput.value = ""; // 清空数据
      // 调用接口
      chatGPTList({
        model: "gpt-3.5-turbo",
        temperature: store.state.temperature, // 温度
        presence_penalty: store.state.presence_penalty, // 存在_惩罚
        frequency_penalty: store.state.frequency_penalty, // 频率惩罚
        stream: true, // 数字流
        messages: chatList.slice(-store.state.messagesNumber), // 截取18条聊天记录 因为接口限制  // 默认15条聊天记录
      })
        .then((res) => {
          if (res && res.status === 200) {
            chatList.push({
              role: "assistant",
              content: "",
            });
            const reader = res.body.getReader();
            const decoder = new TextDecoder();
            return reader.read().then(function processResult(result) {
              if (result.done) {
                MaskLayerShow.value = false; // 关闭遮罩层
                inputInstance.value.focus(); // 获取焦点
                return;
              }
              const chunk = decoder.decode(result.value, { stream: true });
              const jsonStrings = chunk.split("data: "); // 分割成单个JSON字符串的数组
              const jsonObjects = jsonStrings
                .filter((str) => str.trim() !== "" && str.trim() != "[DONE]") // 过滤掉空字符串
                .map((str) => JSON.parse(str)); // 解析每个JSON字符串为对象
              jsonObjects.forEach((res) => {
                if (res.choices[0].delta.content) {
                  chatList[chatList.length - 1].content +=
                    res.choices[0].delta.content;
                  localStorage.setItem("chat", JSON.stringify(chatList));
                }
              });
              return reader.read().then(processResult);
            });
          } else {
            MaskLayerShow.value = false; // 关闭遮罩层
            ElMessage({
              showClose: true,
              message: "服务器连接失败请检查网络",
              type: "error",
              duration: 5000,
            });
          }
        })
        .catch((err) => {
          MaskLayerShow.value = false; // 关闭遮罩层
          console.log(err);
          switch (err.request.status) {
            case 500:
              ElMessage({
                showClose: true,
                message: "服务器在处理您的请求时出错",
                type: "error",
                duration: 5000,
              });
              break;
            case 401:
              ElMessage({
                showClose: true,
                message: "无效的身份验证",
                type: "error",
                duration: 5000,
              });
              break;
            case 429:
              ElMessage({
                showClose: true,
                message: "引擎当前超载，请稍后再试",
                type: "error",
                duration: 5000,
              });
              break;
            default:
              ElMessage({
                showClose: true,
                message: "未知错误",
                type: "error",
                duration: 5000,
              });
              break;
          }
        });
    };
    // const WordForWord = (text) => {
    //   let i = 0;
    //   // 让数据逐字显示在页面上
    //   function WordForWord() {
    //     chatList[chatList.length - 1].content = text.substring(0, i++) + "-";
    //     localStorage.setItem("chat", JSON.stringify(chatList));
    //     if (i === text.length) {
    //       clearTimeout(time); // 清除定时器
    //       chatList[chatList.length - 1].content = text; // 全部的文本复制给 chatList
    //       localStorage.setItem("chat", JSON.stringify(chatList)); // 存储到本地
    //       MaskLayerShow.value = false; // 关闭遮罩层
    //     }
    //   }
    //   // 定义time 方便清除定时器
    //   time = setInterval(WordForWord, 30);
    // };
    // 点击停止生成按钮
    const StopGenerate = () => {
      store.state.stopTextStream.abort(); // 停止接受文本流
      MaskLayerShow.value = false; // 关闭遮罩层
    };
    // 输入框聚焦平滑到底部
    const scrollToBottom = () => {
      let chatBodyUl = document.getElementsByClassName("chatBody");
      const scrollHeight = chatBodyUl[0].childNodes[0].offsetHeight;
      chatBodyUl[0].scrollTo({
        left: 0,
        top: scrollHeight,
        behavior: "smooth", // 平滑过度
      });
    };
    // 录音文本触发事件
    const RecordingText = (e) => {
      chatInput.value = e; // 输入框赋值语音文本
      // inputInstance.value.focus(); // 获取焦点
      enterEvent(); // 也不获取焦点了直接发送
    };
    onMounted(() => {
      const chatBodyUl = document.getElementsByClassName("chatBodyUl"); // 获取ul节点
      let LastOne;
      // 监听ul变化滑动到底部
      const observer = new MutationObserver((mutations) => {
        if (LastOne === mutations[0].target.offsetHeight) {
          LastOne = mutations[0].target.offsetHeight;
        } else {
          LastOne = mutations[0].target.offsetHeight;
          scrollToBottom();
        }
      });
      // 具体监听的类型
      observer.observe(chatBodyUl[0], {
        childList: true, // 子节点的变动（新增、删除或者更改）
        attributes: true, // 属性的变动
        characterData: true, // 节点内容或节点文本的变动
        subtree: true, // 是否将观察器应用于该节点的所有后代节点
      });
      // 为什么放在onMounted里边是要等dom加载完毕ul会触发滚动
      // 接受vuex从首页传递过来的数据
      // 如果vuex里边有值就赋值给聊天框 刷新之后里边的值会消失所以就不会触发了
      if (store.state.InputSearch) {
        chatInput.value = store.state.InputSearch;
        enterEvent(); // 发起请求
      }
    });
    // 切换语音状态
    let MaskLayerRelationship = ref(null);
    // 遮罩层显示状态 必须要写两个因为没办法不能直接修改他的状态会导致监听失败
    const switchVoice = (e) => {
      WhetherToDisplayOrNot.value = e;
      MaskLayerRelationship.value = e;
    };
    // 监听 遮罩层是否变化
    watch(MaskLayerShow, (newVal) => {
      if (newVal) {
        if (MaskLayerRelationship.value) {
          WhetherToDisplayOrNot.value = false;
        }
      }
      if (!newVal) {
        if (MaskLayerRelationship.value) {
          WhetherToDisplayOrNot.value = true;
        }
      }
    });
    return {
      chatList,
      chatInput,
      enterEvent,
      imgLoad,
      MaskLayerShow,
      StopGenerate,
      scrollToBottom,
      ImPicture,
      inputInstance,
      RecordingText,
      switchVoice,
      RecordingStatus,
      WhetherToDisplayOrNot,
    };
  },
};
</script>

