<script>
import { ref } from "vue";
import { useStore } from "vuex";

export default {
  name: "rightFunction",
  setup() {
    let store = useStore();
    let messagesNumber = ref(12); // 默认15条聊天记录
    store.commit("messagesNumber", messagesNumber);
    let temperature = ref(0.2); // 要使用的采样温度，介于0和2之间。较高的值(如0.8)将使输出更具随机性，而较低的值(如0.2)将使输出更具针对性和确定性。
    store.commit("temperature", temperature);
    let presence_penalty = ref(0); // 介于-2.0和2.0之间的数字。正值根据新词到目前为止是否出现在文本中来惩罚它们，从而增加了模型讨论新主题的可能性。
    store.commit("presence_penalty", presence_penalty);
    let frequency_penalty = ref(0); // 介于-2.0和2.0之间的数字。到目前为止，正值根据新标记在文本中的现有频率来惩罚它们，从而降低了模型逐字重复相同行的可能性。
    store.commit("frequency_penalty", frequency_penalty);
    return { temperature, presence_penalty, frequency_penalty, messagesNumber };
  },
};
</script>

