<template>
  <div>
    <img
      :src="switchVoiceForStatus ? voice : Keyboard"
      @click="switchVoice"
      class="chatSearchVoice"
    />
  </div>
  <!--<audio :src="audioSrc" controls></audio>-->
</template>

<script>
import { ref, watch } from "vue";
import { useStore } from "vuex";
import { GetRecordingText } from "@/api/RecordingAxiosApi";
import voice from "@/assets/images/语音.png";
import Keyboard from "@/assets/images/键盘.png";
// element组件
import { ElMessage } from "element-plus";
export default {
  name: "GetRecording",
  props: ["RecordingStatus", "MaskLayerShow"],
  setup(props, { emit }) {
    let store = useStore();
    const audioSrc = ref("");
    const chunks = ref([]); // 保存的音频
    const getAudioTracks = ref(null); // 关闭录音  获取音轨
    const recorder = ref(""); // 创建MediaRecorder对象
    const switchVoiceForbidden = ref(true); // 切换语音禁用
    const switchVoiceForStatus = ref(false); // 切换语音状态
    // 开始录制
    const startRecording = () => {
      switchVoiceForbidden.value = false; // 如果开始录制了就不让点击图片按钮了
      // 开启麦克风
      navigator.mediaDevices
        .getUserMedia({ audio: true })
        .then(function (stream) {
          getAudioTracks.value = stream.getAudioTracks()[0]; // 获取音频轨道 用来停止录制
          // 创建MediaRecorder对象
          recorder.value = new MediaRecorder(stream);
          // 开始录制
          recorder.value.start();
          recorder.value.ondataavailable = function (event) {
            // 将数据转换为Blob对象
            chunks.value = new Blob([event.data], { type: "audio/wav" });
          };
          // 停止时触发
          recorder.value.addEventListener("stop", () => {
            store.commit("PhonetisStyleStatus", true); // 修改状态
            // 赋值给audio音频
            // let url = URL.createObjectURL(chunks.value);
            // audioSrc.value = url;
            // 释放URL对象
            // URL.revokeObjectURL(url);
            // 拿到 base64 格式音频
            blobToBase64(chunks.value, (res) => {
              // 拿到TaskId
              GetRecordingText({
                EngineModelType: "16k_zh",
                ChannelNum: 1,
                ResTextFormat: 0,
                SourceType: 1,
                Data: res,
              })
                .then((res) => {
                  const str = res.data;
                  // 正则去掉不需要的字符
                  const result = str.replace(
                    /\[\d+:\d+\.\d+,\d+:\d+\.\d+\]\s*/g,
                    ""
                  );
                  // 如果返回的文本为空也解除 键盘活语音输入状态
                  if (result.trim() === "") {
                    switchVoiceForbidden.value = true;
                    ElMessage("语音识别为空请靠近麦克风重试！");
                  }
                  emit("RecordingText", result); // 调用RecordingText // 把文字给输入框
                  store.commit("PhonetisStyleStatus", false); // 修改状态在vuex里边的状态 什么时候关闭语音遮罩层  是请求活来文本的时候关闭
                })
                .catch((err) => {
                  console.log(err);
                });
            });
          });
        });
    };
    // 结束录制
    const stopRecording = () => {
      // 停止录制
      recorder.value.stop();
      getAudioTracks.value.stop(); // 停止录制
    };
    // 图片的变化 是否显示按下说话
    const switchVoice = () => {
      // 当点击开始录音的时候就不能改变他的状态了
      if (switchVoiceForbidden.value) {
        switchVoiceForStatus.value = !switchVoiceForStatus.value;
        emit("switchVoice", switchVoiceForStatus.value); // 发放当前的状态给 chat组件
      }
    };
    // blob转换为 base64 编码
    function blobToBase64(blob, callback) {
      var reader = new FileReader();
      reader.onload = function () {
        var dataUrl = reader.result;
        var base64 = dataUrl.split(",")[1];
        callback(base64);
      };
      reader.readAsDataURL(blob);
    }
    // 监听PhoneticStyle 是否按下了
    // 遇到的bug 要监听对象中的某一个属性的变化需要是一个函数返回值的形式就是 props.RecordingStatus
    watch(
      () => props.RecordingStatus,
      (newVal) => {
        if (newVal) {
          startRecording();
        } else {
          stopRecording();
        }
      }
    );
    // 当chat回答完毕的时候触发的  这个是监听是否遮罩层关闭 也就是是否回答完毕之后在回答完毕的时候才关闭遮罩层
    watch(
      () => props.MaskLayerShow,
      (newVal) => {
        if (!newVal) {
          switchVoiceForbidden.value = true; // 回答完毕的时候才可以解除点击限制  切换语音还是键盘输入
        } else {
          switchVoiceForbidden.value = false; // 当正在等待请求结果时禁用切换  切换语音还是键盘输入
        }
      }
    );
    return {
      startRecording,
      stopRecording,
      audioSrc,
      voice,
      switchVoice,
      Keyboard,
      switchVoiceForbidden,
      switchVoiceForStatus,
    };
  },
};
</script>

<style lang="scss" scoped>
.chatSearchVoice {
  transform: translate(-100%, 50%);
  height: 0.53rem;
  position: absolute;
  cursor: pointer;
  bottom: 50%;
}
</style>
