<template>
  <div class="PhoneticMaskLayer">
    <span v-if="!srcLoadingOrshow" @click="clickBut">{{
      !clickRecording ? "点击开始识别语音" : "完毕开始转文字"
    }}</span>
    <img v-else :src="srcLoading" alt="" />
  </div>
</template>

<script>
import { ref, watch } from "vue";
import { useStore } from "vuex";
import srcLoading from "@/assets/images/loadding.gif";
export default {
  name: "PhoneticStyle",
  setup(props, { emit }) {
    let store = useStore();
    const RecordingStatus = ref(null);
    const srcLoadingOrshow = ref(null);
    const clickRecording = ref(false);
    // 点击第一次开始录音 点击第二次发送录音请求获取数据
    const clickBut = () => {
      clickRecording.value = !clickRecording.value;
      if (clickRecording.value) {
        startRecording();
      } else {
        srcLoadingOrshow.value = true; // 防止跳动
        stopRecording();
      }
    };
    // 开始录制
    const startRecording = () => {
      // 最终目的就是传递状态
      emit("update:RecordingStatus", (RecordingStatus.value = true));
    };
    // 结束录制
    const stopRecording = () => {
      // 最终目的就是传递状态
      emit("update:RecordingStatus", (RecordingStatus.value = false));
    };
    // 监听vuex中的状态值 使用显示loading
    watch(
      () => store.state.PhonetisStyleStatus,
      (newVal) => {
        srcLoadingOrshow.value = newVal; // loading 是否显示
      }
    );
    return { srcLoading, srcLoadingOrshow, clickBut, clickRecording };
  },
};
</script>

<style scoped lang="scss">
.PhoneticMaskLayer {
  width: 100%;
  height: 0.83rem;
  position: absolute;
  background: white;
  z-index: 2;
  top: 0;
  opacity: 0.9;
  span {
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 0.3rem;
  }
  img {
    width: 2rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
